
import { Link } from '@/types'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'Breadcrumbs',
  props: {
    links: {
      type: Array,
      required: true
    }
  },
  computed: {
    lastLink (): Link {
      return (this.links as Link[])[this.links.length - 1]
    },
    linksMinusLast (): Link[] {
      return (this.links as Link[]).slice(0, this.links.length - 1)
    }
  }
})
